define('ch-ember-app/components/ch-content-item', ['exports'], function (exports) {
  'use strict';

  Object.defineProperty(exports, "__esModule", {
    value: true
  });
  var Component = Ember.Component,
      computed = Ember.computed,
      inject = Ember.inject,
      get = Ember.get,
      set = Ember.set;
  exports.default = Component.extend({

    constants: inject.service(),
    /**
     * (Required) Entity model object to render.
     *
     * @property item
     * @type {Object}
     * @public
     */
    item: null,

    /**
     * The property to retrieve from an option to use for filtering and sorting.
     * @property emptyLabel
     * @type {String}
     * @public
     * @default n/a
     */
    emptyLabel: 'n/a',

    sources: null,

    tags: null,

    showExtraInfo: false,

    isVersion1: null,

    /**
     * The id of the currently selected item.
     *
     * @property selectedItemId
     * @type {String}
     * @public
     */
    selectedItemId: null,

    maxTitleLength: 35,

    sourceName: computed('item', 'sources.[]', function () {
      var orignKey = get(this, 'item.origin');
      if (orignKey === undefined) {
        return this.get('emptyLabel');
      }
      var client = get(this, 'sources').findBy('uuid', orignKey);
      if (client === undefined) {
        return this.get('emptyLabel');
      }
      return get(client, 'name') || this.get('emptyLabel');
    }),

    truncatedTitle: computed(function () {
      var title = get(this.item, 'title');
      if (title.length <= this.maxTitleLength) {
        return title;
      }
      return title.substring(0, this.maxTitleLength) + "...";
    }),

    selectedEntities: [],

    entitySelected: computed(function () {
      var item = get(this, 'item');
      return get(this, 'selectedEntities').includes(item.id);
    }),

    numSelectedEntities: 0,

    importButtonLabel: '',

    actions: {
      toggleExtraInfo: function toggleExtraInfo() {
        set(this, 'showExtraInfo', !get(this, 'showExtraInfo'));
      },
      onItemSelected: function onItemSelected(selectedId) {
        this.set('selectedItemId', selectedId);
      },
      checkboxChanged: function checkboxChanged(value) {
        // Update the array by adding or removing from the selected Entities
        var selectedEntities = get(this, 'selectedEntities');
        if (value.checked) {
          selectedEntities.push(value.id);
          set(this, 'selectedEntities', selectedEntities);
        }
        if (!value.checked) {
          var index = selectedEntities.indexOf(value.id);
          if (index !== -1) {
            selectedEntities.splice(index, 1);
          }
          set(this, 'selectedEntities', selectedEntities);
        }
        // Update the button title, with the number of items selected
        set(this, 'numSelectedEntities', selectedEntities.length);

        // Update the lable for the import button
        var entities = get(this, 'numSelectedEntities');
        if (entities === 0) set(this, 'importButtonLabel', '');
        if (entities === 1) set(this, 'importButtonLabel', ': 1 Item');
        if (entities > 1) set(this, 'importButtonLabel', ': ' + entities + ' Items');
      },
      switchTitleLanguage: function switchTitleLanguage(language) {
        var labelByLang = this.get('item.labelByLang');
        var label = labelByLang[language.language];
        var len = label.length;
        if (len > this.maxTitleLength) {
          label = label.substring(0, this.maxTitleLength) + "...";
        }
        this.$('.ch__cmp__content-item__title .uk-card-title').text(label);
      }
    },

    classNames: ['ch__cmp__content-item']
  });
});